<template>
  <div>
   	<div class="comtent" :style="comtent_style" @mousedown="changebackground($event)" >
	      {{msg1}}{{msg2}}{{msg3}}
        <div>{{msg4}}</div>
        <div>{{msg5}}</div>
	    </div>
	    <div class="enlp" v-show="open_state">
        给宝宝的一封信
        <div class="open" @click="open">开</div>
      </div>
	    <div class="reset" @click="reset" v-show="reset_state">重置</div>  
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
     	msg1: '',
	      msg2: '',
	      msgs: '',
	      msgs1: '',
	      open_state: 1,
	      comtent_style: {
	        background: '#fff',
	        color: '#fff',
	      },
    }
  },
  methods: {
    print (msg,msgs,speed) {
      return new Promise((resolve) => {
        var len = 0;
        var timer = setInterval(() => {
          this[msg] = msgs.substring(0,len)+'_';
          if (len <= msgs.length){
            len++;
          } else {
            clearInterval(timer)
            this[msg] = msgs.substring(0,len)
            resolve();
          }
        },speed)
      })
    },
    del (msg,msgs,num,speed) {
      return new Promise((resolve) => {
        var len = msgs.length
        var timer = setInterval(() => {
          this[msg] = msgs.substring(0,len)+'_';
          if (len > msgs.length-num) {
            len--
          } else {
            clearInterval(timer)
				    this[msg]=msgs.substring(0,len)
            resolve();
          }
        },speed)
      })
    },
    tingliu (msg,speed,num) {
      return new Promise((resolve) => {
        var len = 0;
        var timer = setInterval(() => {
          this[msg] = '_'
          len++
          console.log(len)
          if (len%2 == 0) {
            this[msg] = ''
          }else{
            this[msg] = '_'
          }
          if (num < len) {
            clearInterval(timer)
            resolve();
          }
        },speed)
      })
    },
    open () {
      this.open_state = 0;
      this.msgs = '嗨！很抱歉过了这么久才给你这个惊喜，不知道你喜不喜欢，或许我不该再来打扰你，让你烦心，但是我想完成这个作品，将它展示给你。\
      这么久以来你一直陪伴这我，在我最艰难的时候，你鼓励我，安慰我，让我度过不快乐的日子,我要给你说一声谢谢。我是一个不成熟的孩子，有时候会感情用事，所以会伤害到你，我要给你说一声对不起。\
      可能我真的不明白你要的是什么，我错误的理解了你把我当作亲人的感情，导致这一次这么严重的结果，我始料未及。我以为你会一直都在。我以为我可以一直是叔叔。\
      当一个人拥有某一样东西的时候并不知道幸福，一旦失去了，才知道原来我很幸福，这大概就是珍惜为什么这么可贵，而我就是那个不懂得珍惜的人。我可能真的是习惯你了，我也开始理解你了。'
      this.msgs1 = '宝宝，请不要离开我，是我的错'
      this.msgs2 = '对不起，这么久才给你这个惊喜'
      this.msgs3 = '啊啊啊啊啊。怎么开头啊！！！！'
      this.msgs4 = '啊啊啊，好捉急，表达不出来内心的感情！！fuck！！'
      this.msgs5 = '容我思考一会。。'
      this.msgs6 = '就让我继续做你的叔叔吧，直面内心，我已经把你视为亲人了，之前是我无理取闹，我是真的不想和你走到形同陌路的地步，如果你还愿意，就让我留在你身边，如果你真的决心不想理我了，我也就安安静静的不再出现'
      this.msgs7 = '此致'
      this.msgs8 = '---于2017.3.2日凌晨'
      this.async()()
      this.comtent_style.background = '#000'
    },
    reset () {
      this.comtent_style.background = "#000"
      this.comtent_style.color = "#fff"
    },
    changebackground (event) {
      var x = event.clientX;
      var _this = this
      document.onmousemove = function(event) {
        if (event.clientX - x > 50){
          document.onmouseup = function() {
            _this.comtent_style.background = "#" + (~~(Math.random() * (1 << 24))).toString(16);
            document.onmousemove = null;
          }
        }
        else if (event.clientX - x < -50) {
          document.onmouseup = function() {
            _this.comtent_style.color = "#" + (~~(Math.random() * (1 << 24))).toString(16);
            document.onmousemove = null;
          }
        }
      }
    },
    display () {
      var _this = this
      setTimeout(()=>{_this.comtent_style.opacity = 0}, 10000)
    },
    async () {
      var _this = this
      return async function () {
        await _this.print('msg1',_this.msgs1,200)
        await _this.del('msg1',_this.msgs1, _this.msgs1.length, 100)
        await _this.print('msg1',_this.msgs2,200)
        await _this.del('msg1',_this.msgs2, _this.msgs2.length, 100)
        await _this.print('msg1',_this.msgs3,200)
        await _this.del('msg1',_this.msgs3, _this.msgs3.length, 100)
        await _this.tingliu('msg1', 400,15)
        await _this.print('msg1',_this.msgs,200)
        await _this.print('msg2',_this.msgs4,200)
        await _this.del('msg2',_this.msgs4, _this.msgs4.length, 100)
        await _this.print('msg2',_this.msgs5,200)
        await _this.tingliu('msg3', 400,15)
        await _this.del('msg2',_this.msgs5, _this.msgs5.length, 100)
        await _this.print('msg2',_this.msgs6,200)
        await _this.print('msg4',_this.msgs7,200)
        await _this.print('msg5',_this.msgs8,200)
        _this.display()
      }
    }
  },
  computed: {
    reset_state () {
      return !this.open_state
    }
  }
}
</script>

<style>
body{
			padding: 0;
      margin: 0;
      color: #fff;
		}
		.comtent{
		    position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    bottom: 0;
		    word-break: break-all;
		    box-sizing: border-box;
		    font-size: 16px;
		    transition: all 1s;
		    padding: 8px;
		    letter-spacing: 1px;
		    line-height: 30px;
		    user-select: none;
		    text-indent: 20px;
        overflow: scroll;
		  }
      .comtent div {
        text-align: right;
      }
      .enlp{
        width: 400px;
		    height: 240px;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    margin: -120px 0 0 -200px;
        text-align: center;
        color: #000;
        font-size: 30px;
      }
		  .open{
		    width: 80px;
		    height: 80px;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    margin: -40px 0 0 -40px;
		    background: #000;
		    border-radius: 40px;
		    text-align: center;
		    line-height: 80px;
		    font-size: 30px;
        color: #fff;
		  }
		  .reset{
		    width: 60px;
		    height: 60px;
		    position: fixed;
		    right: 30px;
		    bottom: 30px;
		    background: #fff;
		    color: #000;
		    border-radius: 30px;
		    text-align: center;
		    line-height: 60px;
		    font-size: 20px;
		  }
</style>



